import { mapActions, mapGetters } from 'vuex'
export default {
    mounted () {
        console.log(this.$store)
    },
    data () {
        return {
            'msg':'aaa'
        }
    },
    computed: {
        ...mapGetters({
            count: 'getCount',
            message: 'getMessage'
        })
    },
    methods: {
        ...mapActions({
            increment: 'INCREMENT',
            decrement: 'DECREMENT',
            changemessage: 'CHANGEMESSAGE'
        })
    }
}



